
/* cursorBall.css */
.cursor-ball {
  background-color: #ffffff;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  pointer-events: none;
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(-50%, -50%);
  mix-blend-mode: difference;
}

/* Media query for smaller screens */
@media (max-width: 768px) {
  .cursor-ball {
    display: none; /* Hide the cursor ball on mobile screens */
  }
}
